.causes-alzheimers {
  padding: 40px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    max-width: 333px;
    width: 100%;
    flex-shrink: 0;
  }
  &__center {
    max-width: 458px;
    margin: 0 5px;
    position: relative;
    img {
      width: 100%;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 1;
      padding: 15px;
    }
    img:nth-child(2) {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__right {
    max-width: 333px;
    width: 100%;
    flex-shrink: 0;
  }
  &__item {
    max-width: 273px;
    width: 100%;
    min-height: 109px;
    height: 100%;
    box-shadow: 3px 1px 25px 2px rgba(0,0,0,0.05);
    border-radius: 6px;
    padding: 5px 63px 5px 30px;
    display: flex;
    align-items: center;
    position: relative;
    &:not(:first-child) {
      margin-top: 80px;
    }
    &2 {
      max-width: 273px;
      width: 100%;
      min-height: 109px;
      height: 100%;
      box-shadow: 3px 1px 25px 2px rgba(0,0,0,0.05);
      border-radius: 6px;
      padding: 5px 30px 5px 63px;
      margin-left: auto;
      display: flex;
      align-items: center;
      position: relative;
    }
    &2 {
      &:not(:first-child) {
        margin-top: 80px;
      }
    }
    &-icon {
      position: absolute;
      top: 50%;
      left: -7px;
      transform: translate(0, -50%);
      max-width: 27px;
      height: auto;
      img {
        width: 100%;
      }
    }
    &-icon2 {
      position: absolute;
      top: 50%;
      right: -80px;
      transform: translate(0, -50%);
      max-width: 126px;
      height: auto;
    }
    &-icon3 {
      position: absolute;
      top: 50%;
      right: -7px;
      transform: translate(0, -50%);
      max-width: 27px;
      height: auto;
    }
    &-icon4 {
      position: absolute;
      top: 50%;
      left: -80px;
      transform: translate(0, -50%);
      max-width: 126px;
      height: auto;
    }
    &-desc {
      font-size: 15px;
      line-height: 140%;
      font-weight: 600;
      color: #2c2c2c;
      max-width: 170px;
    }
  }
  .line {
    &:after {
      content: '';
      position: absolute;
      right: -10px;
      bottom: 15px;
      width: 25px;
      height: 3px;
      background-color: #76c21a;
      transform: rotate(25deg);
    }
  }
  .line2 {
    &:after {
      content: '';
      position: absolute;
      right: -10px;
      top: 15px;
      width: 25px;
      height: 3px;
      background-color: #76c21a;
      transform: rotate(-25deg);
    }
  }
  .line3 {
    &:after {
      content: '';
      position: absolute;
      left: -10px;
      bottom: 15px;
      width: 25px;
      height: 3px;
      background-color: #76c21a;
      transform: rotate(-25deg);
    }
  }
  .line4 {
    &:after {
      content: '';
      position: absolute;
      left: -10px;
      top: 15px;
      width: 25px;
      height: 3px;
      background-color: #76c21a;
      transform: rotate(25deg);
    }
  }
}
.concomitant-diseases {
  padding: 95px 0 40px;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
    grid-auto-rows: 1fr;
  }
  &__item {
    border: 1px solid rgba(0,0,0,0.05);
    box-shadow: 0px 3px 29px 3px rgba(0,0,0,0.02);
    border-radius: 10px;
    padding: 35px 30px 30px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 15px;
      background: #76c21a;
      border-radius:0 0 10px 10px;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 3px;
      transform: translate(-50%, 0);
      width: 100%;
      height: 15px;
      background: #ffffff;
      border-radius:0 0 10px 10px;
    }
    &-icon {
      width: 45px;
      height: 45px;
      background: #76c21a;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: -55px;
      left: 0;
    }
    &-icon2 {
      background: #f7b001;
    }
    &-desc {

    }
    &-desc2 {
      &:after {
        background: #f7b001;
      }
    }
    &-number {
      font-size: 25px;
      line-height: 120%;
      font-weight: 700;
      color: rgba(44, 44, 44, 0.1);
      position: absolute;
      top: 12px;
      right: 12px;
    }
    &-title {
      font-size: 17px;
      line-height: 120%;
      font-weight: 700;
      color: #2c2c2c;
    }
  }
}
.dementia-kinds {
  padding: 40px 0;
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    color: #2c2c2c;
  }
  &__inner {
    display: flex;
    align-items: center;
    margin-top: 45px;
  }
  &__img {
    width: 440px;
    margin-right: 68px;
    position: relative;
    img {
      width: 100%;
      display: block;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 25px;
    width: calc(100% - 508px);
  }
  &__item {
    display: flex;
    align-items: center;
    box-shadow: 3px 1px 25px 2px rgba(0,0,0,0.05);
    border-radius: 6px;
    padding: 20px 27px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 15px;
      background: #76c21a;
      border-radius:0 0 10px 10px;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 3px;
      transform: translate(-50%, 0);
      width: 100%;
      height: 15px;
      background: #ffffff;
      border-radius:0 0 10px 10px;
    }
    &2 {
      &:after {
        background: orange;
      }
    }
    &-icon {
      width: 95px;
      flex-shrink: 0;
      position: relative;
      img {
        width: 100%;
        display: block;
      }
      span {
        font-size: 38px;
        line-height: 120%;
        font-weight: 700;
        text-shadow: 0px 3px 3px rgba(139,179,20,0.41);
        color: #ffffff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -55%);
      }
    }
    &-desc {
      margin-left: 25px;
    }
    &-title {
      font-size: 17px;
      line-height: 120%;
      font-weight: 600;
      color: #2c2c2c;
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      font-weight: 400;
      color: #666666;
      margin-top: 10px;
    }
    &-icon2 {
      position: absolute;
      top: 50%;
      right: -7px;
      transform: translate(0, -50%);
      max-width: 26px;
      img {
        width: 100%;
      }
      .rotate {
        transform: rotate(180deg);
      }
    }
  }
}
.memory-info {
  padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    grid-auto-rows: 1fr;
  }
  &__item {
    box-shadow: 3px 1px 25px 2px rgba(0,0,0,0.05);
    border-radius: 6px;
    &-img {
      img {
        width: 100%;
      }
    }
    &-desc {
      padding: 30px;
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      color: #2c2c2c;
      padding-bottom: 15px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 1px;
        background: rgba(102, 102, 102, 0.1);
      }
      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1px;
        width: 100px;
        height: 3px;
        background: #8bb314;
      }
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      color: rgba(102, 102, 102, 0.7);
      margin-top: 15px;
    }
    &-link {
      display: block;
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      text-decoration: underline;
      color: #8bb314;
      margin-top: 15px;
      background: transparent;
      padding: 0;
    }
  }
}
.cancer-pain {
  padding: 40px 0;
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    grid-auto-rows: 1fr;
  }
  &__item {
    display: flex;
    background-color: #f9fafb;
    border: 1px solid rgba(0,0,0,0.05);
