    }
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      left: 50%;
      bottom: 3px;
      transform: translate(-50%, 0);
      width: 100%;
      height: 15px;
      background:  #f9fafb;
      border-radius:0 0 10px 10px;
    }
    &2 {
