}
.consequences-pain {
  padding: 40px 0;
  &__content {
    background-image: url("../img/consequences_pain__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 40px 80px 40px 240px;
    position: relative;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 32px;
    grid-auto-rows: 1fr;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 2px;
      height: calc(100% - 80px);
      background-color: #88af14;
    }
  }
  &__img {
    width: 318px;
    height: 318px;
    background-image: linear-gradient(-50.0deg, #c7e376 13%, #8bb314 87%);
    box-shadow: 15px 7px 16px 2px rgba(0,0,0,0.07), inset 1px 2px 9px 0px rgba(0,0,0,0.08);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 80px;
    transform: translate(0, -50%);
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 285px;
      height: 286px;
      border: 2px solid #ffffff;
      border-radius: 50%;
      pointer-events: none;
    }
    img {
      max-width: 48px;
      width: 100%;
    }
    span {
      font-size: 22px;
      line-height: 120%;
      font-weight: 800;
      text-transform: uppercase;
      text-shadow: 3px 1px 12px rgba(0,0,0,0.17);
      color: #ffffff;
      margin-top: 15px;
    }
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-info {
      display: flex;
      align-items: center;
      padding-left: 277px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        width: 277px;
        height: 2px;
        background-color: #88af14;
      }
    }
    &-info2 {
      &:after {
        background-color: #f7b001;
      }
    }
    &-number {
      width: 80px;
      height: 80px;
      background-image: linear-gradient(-50.0deg, rgba(255,255,255,0.0) -4%, rgba(244,243,243,0.61) 105%);
      box-shadow: 15px 7px 16px 2px rgba(0,0,0,0.07);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #88af14;
      font-size: 38px;
      line-height: 105%;
      font-weight: 700;
      text-shadow: 0px 3px 3px rgba(240,171,1,0.41);
      color: #ffffff;
    }
    &-number2 {
      background-color: #f7b001;
    }
    &-icons {
      display: flex;
      align-items: center;
      margin-left: 25px;
      img {
        width: auto;
      }
      img:not(:first-child) {
        margin-left: 20px;
      }
    }
    &-desc {
      background-image: linear-gradient(-7.0deg, rgba(255,255,255,0.0) -7%, #f4f3f3 108%);
      box-shadow: 15px 7px 16px 2px rgba(0,0,0,0.02), inset 1px 2px 13px 0px rgba(0,0,0,0.04);
      border-radius: 40px;
      padding: 5px 25px;
      max-width: 354px;
      width: 100%;
      min-height: 80px;
      font-size: 15px;
      line-height: 120%;
      font-weight: 700;
      color: #2c2c2c;
      display: flex;
      align-items: center;
      margin-left: 15px;
    }
  }
}
.concomitant-diseases2 {
  padding: 40px 0;
  overflow: hidden;
  &__wrapp {
    padding: 60px 80px;
    background-image: url("../img/concomitant_diseases2_bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 50px;
    max-width: 782px;
  }
  &__item {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      z-index: 0;
      right: 5px;
      bottom: 15px;
      width: 62px;
      height: 62px;
      box-shadow: 15px 7px 16px 2px rgba(0,0,0,0.02);
      border-radius: 50%;
    }
    &:nth-child(odd) {
      &:after {
        background: #8bb314;
      }
      .concomitant-diseases2__item-number {
        color: #8bb314;
      }
    }
    &:nth-child(even) {
      &:after {
        background: #f7b001;
      }
      .concomitant-diseases2__item-number {
        color: #f7b001;
      }
    }
    &-wrapp {
      min-height: 227px;
      min-width: 227px;
      background: #ffffff;
      box-shadow: 15px 7px 16px 2px rgba(0,0,0,0.02);
      border-radius: 50%;
      padding: 30px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      position: relative;
      z-index: 2;
    }
    &-number {
      position: absolute;
      top: -20px;
      left: 0;
      font-size: 60px;
      line-height: 100%;
      font-weight: 700;
      text-shadow: 0px 3px 3px rgba(139,179,20,0.17);
    }
    &-title {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      color: #2c2c2c;
    }
    &-text {
      font-size: 14px;
      line-height: 140%;
      font-weight: 400;
      color: #666666;
      margin-top: 10px;
    }
  }
}
