@media screen and (max-width: 1100px) {
  .concomitant-diseases {
    &__items {
      gap: 20px;
    }
  }
  .cancer-pain {
    &__item {
      &-number {
        font-size: 30px;
      }
    }
  }
  .consequences-pain {
    &__content {
      padding: 40px 40px 40px 160px;
    }
    &__img {
      left: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .causes-alzheimers {
    &__inner {
      flex-direction: column;
      position: relative;
      padding-right: 415px;
    }
    &__left {
      max-width: 100%;
    }
    &__center {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      max-width: 400px;
    }
    &__right {
      max-width: 100%;
      margin-top: 40px;
    }
    &__item {
      &:not(:first-child) {
        margin-top: 40px;
      }
      &-icon3 {
        left: -7px;
        right: unset;
        transform: translate(0, -50%) rotate(180deg);
      }
      &-icon4 {
        left: unset;
        right: -80px;
      }
    }
    &__item2 {
      padding: 5px 63px 5px 30px;
      margin-left: 0;
      &:not(:first-child) {
        margin-top: 40px;
      }
    }
    .line,
    .line2,
    .line3,
    .line4 {
     &:after {
       display: none;
     }
    }
  }
  .concomitant-diseases {
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
      gap: 75px 30px;
    }
  }
  .dementia-kinds {
    &__img {
      width: 300px;
      margin-right: 30px;
    }
    &__items {
      width: calc(100% - 330px);
    }
  }
  .memory-info {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
  .cancer-pain {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
  .consequences-pain {
    &__content {
      padding: 40px 20px;
    }
    &__img {
      width: 250px;
      height: 250px;
      &:after {
        width: 220px;
        height: 220px;
      }
      span {
        font-size: 16px;
      }
    }
    &__item {
      &-info {
        padding-left: 250px;
        &:after {
          width: 250px;
        }
      }
    }
  }
  .concomitant-diseases2 {
    &__wrapp {
      padding: 40px 20px;
    }
    &__items {
      grid-template-columns: 1fr;
      max-width: 400px;
      margin: 0 auto;
    }
    &__item {
      &:after {
        right: 35px;
      }
      &-wrapp {
        min-width: 100%;
        min-height: 400px;
      }
    }
  }
  .symptoms-diabetes {
    &__content {
      display: block;
      padding: 40px 20px;
    }
    &__left {
      padding-right: 80px;
      padding-left: 7px;
    }
    &__right {
      margin: 37px 0 0 0;
      padding-right: 80px;
      padding-left: 7px;
    }
    &__item {
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .causes-alzheimers {
    &__inner {
      padding-right: 315px;
    }
    &__center {
      max-width: 300px;
    }
  }
  .concomitant-diseases {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
